import {Component, EventEmitter, Input, Output} from '@angular/core';
import {ClockFaceTime} from '../../models/clock-face-time.interface';
import {TimeUnit} from '../../models/time-unit.enum';
import {TimeFormatterPipe} from '../../pipes/time-formatter.pipe';

@Component({
    selector: 'ngx-material-timepicker-dial-control',
    template: `<!--suppress HtmlFormInputWithoutLabel -->
<input class="timepicker-dial__control timepicker-dial__item"
       [ngClass]="{'timepicker-dial__item_active': isActive, 'timepicker-dial__control_editable': isEditable}"
       [(ngModel)]="time" (input)="updateTime()" (focus)="saveTimeAndChangeTimeUnit(timeUnit)"
       (blur)="revertTimeAndFormat()" [readonly]="!isEditable" [timepickerAutofocus]="isActive">
`,
    styles: [`@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700);.timepicker-dial__item{cursor:pointer;color:rgba(255,255,255,.5);font-family:Roboto,sans-serif}.timepicker-dial__item_active{color:#fff}.timepicker-dial__control{border:none;background-color:transparent;font-size:50px;width:60px;padding:0;border-radius:3px}.timepicker-dial__control_editable::-moz-selection,.timepicker-dial__control_editable:focus{color:#00bfff;background-color:#fff;outline:#00bfff}.timepicker-dial__control_editable::selection,.timepicker-dial__control_editable:focus{color:#00bfff;background-color:#fff;outline:#00bfff}`]
})
export class NgxMaterialTimepickerDialControlComponent {

    previousTime: number | string;

    @Input() timeList: ClockFaceTime[];
    @Input() time: number | string;
    @Input() timeUnit: TimeUnit;
    @Input() isActive: boolean;
    @Input() isEditable: boolean;

    @Output() timeUnitChanged = new EventEmitter<TimeUnit>();
    @Output() timeChanged = new EventEmitter<ClockFaceTime>();

    private get selectedTime(): ClockFaceTime {
        return this.timeList.find(t => +t.time === +this.time);
    }

    saveTimeAndChangeTimeUnit(unit: TimeUnit): void {
        this.previousTime = this.time;
        this.timeUnitChanged.next(unit);
    }

    updateTime(): void {
        const time = this.selectedTime;
        if (time && !time.disabled) {
            this.timeChanged.next(time);
            this.previousTime = time.time;
        }
    }

    revertTimeAndFormat(): void {
        const time = this.selectedTime;
        if (!time || time.disabled) {
            this.time = this.previousTime;
        }
        this.time = new TimeFormatterPipe().transform(+this.time, this.timeUnit);
    }
}
