import {ChangeDetectionStrategy, Component, EventEmitter, Input, OnChanges, Output, SimpleChanges} from '@angular/core';
import {TimePeriod} from '../../models/time-period.enum';
import {TimeUnit} from '../../models/time-unit.enum';
import {TimepickerTime} from '../../timepicker-time.namespace';
import {ClockFaceTime} from '../../models/clock-face-time.interface';
import {Moment} from 'moment';

@Component({
    selector: 'ngx-material-timepicker-dial',
    template: `<div class="timepicker-dial">
    <div class="timepicker-dial__time">
        <ngx-material-timepicker-dial-control [timeList]="hours" [time]="hour" [timeUnit]="timeUnit.HOUR"
                                              [isActive]="activeTimeUnit === timeUnit.HOUR"
                                              [isEditable]="isEditable"
                                              (timeUnitChanged)="changeTimeUnit($event)"
                                              (timeChanged)="changeHour($event)">

        </ngx-material-timepicker-dial-control>
        <span>:</span>
        <ngx-material-timepicker-dial-control [timeList]="minutes" [time]="minute" [timeUnit]="timeUnit.MINUTE"
                                              [isActive]="activeTimeUnit === timeUnit.MINUTE"
                                              [isEditable]="isEditable"
                                              (timeUnitChanged)="changeTimeUnit($event)"
                                              (timeChanged)="changeMinute($event)">

        </ngx-material-timepicker-dial-control>
    </div>
    <ngx-material-timepicker-period class="timepicker-dial__period" *ngIf="format !== 24"
                                    [selectedPeriod]="period" [activeTimeUnit]="activeTimeUnit"
                                    [maxTime]="maxTime" [minTime]="minTime" [format]="format"
                                    [hours]="hours" [minutes]="minutes" [selectedHour]="hour"
                                    (periodChanged)="changePeriod($event)"></ngx-material-timepicker-period>
</div>
`,
    styles: [`@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700);.timepicker-dial{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:end;-ms-flex-pack:end;justify-content:flex-end;-webkit-tap-highlight-color:transparent}.timepicker-dial__time{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:baseline;-ms-flex-align:baseline;align-items:baseline;line-height:normal;font-size:50px;color:rgba(255,255,255,.5);font-family:Roboto,sans-serif}.timepicker-dial__period{display:block;margin-left:10px}@media (max-width:1023px) and (orientation:landscape){.timepicker-dial{-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column}.timepicker-dial__period{margin-left:0}}`],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class NgxMaterialTimepickerDialComponent implements OnChanges {

    timeUnit = TimeUnit;

    hours: ClockFaceTime[];
    minutes: ClockFaceTime[];

    @Input() hour: number | string;
    @Input() minute: number | string;
    @Input() format: number;
    @Input() period: TimePeriod;
    @Input() activeTimeUnit: TimeUnit;
    @Input() minTime: Moment;
    @Input() maxTime: Moment;
    @Input() isEditable: boolean;
    @Input() minutesGap: number;

    @Output() periodChanged = new EventEmitter<TimePeriod>();
    @Output() timeUnitChanged = new EventEmitter<TimeUnit>();
    @Output() hourChanged = new EventEmitter<ClockFaceTime>();
    @Output() minuteChanged = new EventEmitter<ClockFaceTime>();

    ngOnChanges(changes: SimpleChanges) {
        if (changes['period'] && changes['period'].currentValue
            || changes['format'] && changes['format'].currentValue) {
            const hours = TimepickerTime.getHours(this.format);

            this.hours = TimepickerTime.disableHours(hours, {
                min: this.minTime,
                max: this.maxTime,
                format: this.format,
                period: this.period
            });
        }
        if (changes['period'] && changes['period'].currentValue
            || changes['hour'] && changes['hour'].currentValue) {
            const minutes = TimepickerTime.getMinutes(this.minutesGap);

            this.minutes = TimepickerTime.disableMinutes(minutes, +this.hour, {
                min: this.minTime,
                max: this.maxTime,
                format: this.format,
                period: this.period
            });
        }
    }

    changeTimeUnit(unit: TimeUnit): void {
        this.timeUnitChanged.next(unit);
    }

    changePeriod(period: TimePeriod): void {
        this.periodChanged.next(period);
    }

    changeHour(hour: ClockFaceTime): void {
        this.hourChanged.next(hour);
    }

    changeMinute(minute: ClockFaceTime): void {
        this.minuteChanged.next(minute);
    }
}
